@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:ital,wght@0,400;0,600;1,500&family=Josefin+Sans:wght@400;600&display=swap);
.comments__container{display:flex;flex-direction:column;margin-bottom:10px;padding:10px;border-radius:4px;background-color:#f0f2f8;box-shadow:2px 4px 2px #d4d3d3;color:black;transition-duration:0, 3s}.comments__container:hover{-webkit-transform:scale(102%);transform:scale(102%)}.comments__name{margin-bottom:8px;font-size:24px;text-transform:uppercase}.comments__text{font-size:16px;margin-bottom:36px}.comments__email{font-size:16px;align-self:flex-end}

.name{font-size:18px;font-weight:600}.email{font-size:16px;margin-bottom:10px}

.post{overflow:hidden;width:100%;margin-bottom:30px;padding:20px;background-color:#bdfde8}.post__image{display:flex;margin:0 auto;width:40%}.post__title{font-size:20px;font-weight:600;text-transform:uppercase}.post__body{font-size:18px}

.postList__container{max-width:900px;padding:40px;margin:10px auto;border-radius:4px}

body,h1,h2,h4{margin:0;box-sizing:border-box;font-family:"Barlow Condensed", sans-serif}.App__title{display:flex;justify-content:center;margin-top:10px}

